import * as React from 'react';
import { render, fireEvent, screen } from '@testing-library/react';
import '@testing-library/jest-dom';

import { SplitButton } from '../index';

test('SplitButton: should render content.', async () => {
  render(<SplitButton>Hello</SplitButton>);
  expect(screen.queryByText('Hello')).toBeInTheDocument();
});

test('SplitButton: should render secondary content.', async () => {
  render(<SplitButton secondary={<div>World</div>} secondaryVisible={true}>Hello</SplitButton>)
  expect(screen.queryByText('World')).toBeInTheDocument();
});

test('SplitButton: should trigger visible change when click secondary', async () => {
  const fn = jest.fn();
  const res = render(<SplitButton onSecondaryVisibleChange={fn} secondary={<div>World</div>} secondaryVisible={true}>Hello</SplitButton>);
  const icon = res.container.querySelector('svg')!;
  fireEvent.click(icon);
  expect(fn).toHaveBeenCalled();
});